<template>
  <div class="card">
		<div class="card__inner">
			<div class="card__media">
        <div :class="img_aspect">
          <picture>
            <source :srcset="img_srcset" media="(min-width: 510px)">
            <img :src="img_src" :alt="img_alt">
          </picture>
        </div>
			</div>
			<div class="card__body">
				<h3 class="card__title">{{ title }}</h3>
				<p class="card__text">{{ description }}</p>
				<button v-on:click="handleOverlayTrigger" class="card__overlay__trigger" tabindex="0">
					<span class="sr-only">View {{ title }} details</span>
					<IconMore fill="#ffffff"></IconMore>
				</button>
			</div>
		</div>
		<div class="card__overlay" v-bind:class="{ active: cardOverlayOpen }">
			<div class="card__overlay__inner">
				<h3>Accomplishments</h3>
				<ul class="list">
          <li v-for="listItem in accomplishmentsArray" v-bind:key="listItem">
            <p>
              {{ listItem }}
            </p>
          </li>
				</ul>
				<footer class="card__overlay__footer">
					<ul>
						<li><a :href="live_href" class="btn">See it live</a></li>
						<li><a :href="git_href" class="btn">View Git</a></li>
					</ul>
				</footer>
				<button v-on:click="toggleOverlay" class="card__overlay__dismiss" tabindex="0">
					<span class="sr-only">Close {{ title }} details</span>
          <IconClose></IconClose>
				</button>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'CardWork',
  props: [
    'title',
    'description',
    'img_srcset',
    'img_src',
    'img_alt',
    'img_aspect',
    'accomplishments',
    'live_href',
    'git_href'
  ],
  data: function () {
    return {
      cardOverlayOpen: false,
      accomplishmentsArray: []
    }
  },
  created () {
    this.accomplishmentsArray = this.accomplishments.split(',')
  },
  methods: {
    toggleOverlay: function () {
      this.cardOverlayOpen = !this.cardOverlayOpen
    },
    handleOverlayTrigger: function (event) {
      this.toggleOverlay()

      event.target.classList.add('is--clicked')
      setTimeout(function () {
        event.target.classList.remove('is--clicked')
      }, 250)
    }
  }
}
</script>
