<template>
  <div class="card">
		<div class="card__inner">
			<div class="card__media">
        <div :class="img_aspect">
          <picture>
            <source :srcset="img_srcset" media="(min-width: 480px)">
            <img :src="img_src" :alt="img_alt">
          </picture>
        </div>
			</div>
			<div class="card__body">
        <h3 class="card__title">{{ title }}</h3>
        <a v-on:click="handleExternalClick" :href="url" class="card__external-link" target="_blank">
          <span class="sr-only">View {{ title }} on CodePen</span>
          <IconExternal fill="#ffffff"></IconExternal>
        </a>
			</div>
		</div>
	</div>
</template>

<script>
import IconExternal from '../icons/IconExternal.vue'

export default {
  name: 'CardPen',
  props: [
    'title',
    'url',
    'img_srcset',
    'img_src',
    'img_alt',
    'img_aspect'
  ],
  components: {
    IconExternal
  },
  methods: {
    handleExternalClick: function (event) {
      event.target.classList.add('is--clicked')
      setTimeout(function () {
        event.target.classList.remove('is--clicked')
      }, 250)
    }
  }
}
</script>
