<template>
  <div class="container">
    <vue-headful
      title="Home | Rhys Matthew"
      description="Home Description"
    />

    <Hero></Hero>

    <section class="section section--lg section--work">
      <div class="section__header text--center-desktop-down">
        <h2>Latest Work</h2>
      </div>
      <div class="cards">
        <div class="mw--600-desktop-down">
          <div class="row">
            <div class="col-xs-12 col-lg-6 mb-2" v-for="portfolioItem in portfolioItems" :key="portfolioItem.data.id">
              <CardWork :key="portfolioItem.data.id" :title="portfolioItem.data.title" :description="portfolioItem.data.description" :img_srcset="portfolioItem.data.img.lg.url" :img_src="portfolioItem.data.img.url" :img_alt="portfolioItem.data.img.alt" img_aspect="aspect--3-2 aspect--1-1-mobileLandscape-down" :live_href="portfolioItem.data.live_link" :git_href="portfolioItem.data.git_link" :accomplishments="portfolioItem.data.accomplishments"></CardWork>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="section section--lg section--experiments">
      <div class="section__header text--center-desktop-down">
        <h2>Experiments</h2>
      </div>
      <div class="mw--600-desktop-down">
        <div class="row center-xs">
          <div class="col-lg-4 mb-2" v-for="codepenItem in codepenItems" :key="codepenItem.data.id">
            <CardPen :key="codepenItem.data.id" :title="codepenItem.data.title" :img_srcset="codepenItem.data.img.lg.url" :img_src="codepenItem.data.img.url" :img_alt="codepenItem.data.img.alt" img_aspect="aspect--3-2 aspect--1-1-mobileLandscape-down" :url="codepenItem.data.url.url"></CardPen>
          </div>
        </div>
      </div>
    </section>

    <section class="section section--lg bg--gradient text--light mt-4-desktop-up">
      <div class="mw--1000">
        <div class="section__block pr-2 pl-2">
          <div class="mw--700-desktop-down">
            <div class="row">
              <div class="col-md-6">
                <h2 class="h2">Let's make something great!</h2>
              </div>
              <div class="col-md-6">
                <p class="mb-2">If you've made it this far down why not send me an email? I'm currently booking in new projects starting from <strong>January</strong> so don't miss out! Let's work together.</p>
                <router-link to="/contact" exact class="btn text--light mb-4">Get in touch</router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Hero from '../components/hero/Hero.vue'
import CardWork from '../components/cards/CardWork.vue'
import CardPen from '../components/cards/CardPen.vue'

export default {
  name: 'Home',
  data: function () {
    return {
      portfolioItems: {},
      codepenItems: {}
    }
  },
  methods: {
    getPortfolioItems: function () {
      this.$prismic.client.query(
        this.$prismic.Predicates.at('document.type', 'portfolio_item'), { orderings: '[portfolio_item.date]' }).then((response) => {
        this.portfolioItems = response.results
      })
    },
    getCodepenItems: function () {
      this.$prismic.client.query(
        this.$prismic.Predicates.at('document.type', 'codepen_item'), { orderings: '[codepen_item.date]' }).then((response) => {
        this.codepenItems = response.results
      })
    }
  },
  created () {
    this.getPortfolioItems()
    this.getCodepenItems()
  },
  mounted () {
    // eslint-disable-next-line
    setTimeout (() => {
      const pageChange = new Event('pageChange')
      document.dispatchEvent(pageChange)
    }, 1500)
  },
  components: {
    Hero,
    CardWork,
    CardPen
  }
}
</script>
